/*====================================
    Google Font  
===================================*/
@import url('https://fonts.googleapis.com/css?family=DM+Sans:400,400i,500,500i,700&display=swap');


/* 
font-family: 'DM Sans', sans-serif;

*/

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}
.clearfix:after {
    clear: both;
}
.fix {
    overflow: hidden;
}

.slick-initialized .slick-slide {
    margin-bottom: -10px;
}




/*===============================
    Background Color 
=================================*/

@include config-bg-colors('bg-color-',
'primary'$primary-color,
'secondary'$secondary-color,
'tertiary'$tertiary-color,
'extra01'$extra01-color,
'extra02'$extra02-color,
'black'$black-color,
'darker'$darker-color,
'darkest'$darkest-color,
'dark'$dark-color,
'mid'$mid-color,
'light'$light-color,
'lighter'$lighter-color,
'lightest'$lightest-color,
'white'$white-color,
'tealLighter'$tealLighter-color,
'extra09'$extra09-color,
'extra010'$extra09-color,
'extra11'#F6F5FA

