/*===========================
    Background Image 
=============================*/
.bg_image {
    @extend %bgImagePosition;
}

@for $i from 1 through 2 {
    .bg_image--#{$i} {
        background-image: url(../images/bg/bg-image-#{$i}.svg);
    }
}

@for $i from 1 through 80 {
    .font--#{$i}{
        font-size: #{$i}px !important;
    }
}


/*=========================
    Text specialized 
==========================*/
.text-italic {
    font-style: italic;
}
.text-normal {
    font-style: normal;
}
.text-underline {
    font-style: underline;
}


/* Height and width */
.fullscreen {
    min-height: 100vh;
    width: 100%;
}
.container, 
.container-fluid, 
.container-lg, 
.container-md, 
.container-sm, 
.container-xl, 
.container-xxl {
    padding-right: 15px;
    padding-left: 15px;
}

.row {
    margin-right: -15px;
    margin-left: -15px;
    >[class*="col"] {
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media only screen and (min-width: 1200px) {
    .container {
        max-width: 1290px;
    }
}

